<template>
    <view class="tabbar">
        <view class="tabbar-item"
            :class="{ 'tabbar-item-active': pageComponent == 'pageExchange', 'tabbar-item-no-active': pageComponent != 'pageExchange' }"
            @click="tabbarClick('pageExchange')">
            <view class="tabbar-item-icon">
                <image class="img" mode="widthFix" :src="$kt.file.byPath('image/icon/exchangeInShop.svg')" />
            </view>
            <view class="tabbar-item-text">兑换</view>
        </view>

        <view class="tabbar-item"
            :class="{ 'tabbar-item-active': pageComponent == 'pageMyInfo', 'tabbar-item-no-active': pageComponent != 'pageMyInfo' }"
            @click="tabbarClick('pageMyInfo')">
            <view class="tabbar-item-icon">
                <image class="img" 
                style="filter: invert(100%);"
                mode="widthFix" :src="$kt.file.byPath('image/tabbar/home.svg')" />
            </view>
            <view class="tabbar-item-text">我的信息</view>
        </view>

        <view class="tabbar-item"
            :class="{ 'tabbar-item-active': pageComponent == 'pageMy', 'tabbar-item-no-active': pageComponent != 'pageMy' }"
            >
            <view class="tabbar-item-icon">
            {{ balanceSelfMap.charmPoints }}
            </view>
            <view class="tabbar-item-text">情趣碎片</view>
        </view>
    </view>
</template>

<script>
export default {
    props: {
        pageComponent: {
            type: String,
            default: "pageExchange"
        }
    },
    data() {
        let appFront = this.$kt.i18n.getAppFront();
        return {
            appFront,
            unreadMessageCount: 0,
            balanceSelfMap:{
                charmPoints:0
            }
        }
    },
    created(){
        this.getBalanceSelfMap();
    },
    mounted() {
        this.unreadMessageCount = this.$ovo.getUnreadMessageCount();
        uni.$on("changeUnreadMessageCount", () => {
            this.unreadMessageCount = this.$ovo.getUnreadMessageCount();
        });
        uni.$on("changeBalanceSelfMap",()=>{
            this.balanceSelfMap = this.$system.getBalanceSelfMap();
        });
    },
    methods: {
        tabbarClick(text) {
            this.$emit('tabbarClick', text);
            if (text == "pageMyInfo") {
                if(this.pageComponent!="pageMyInfo"){
                    uni.$emit("shopPageMyInfoRefresh");                    
                }

                // 如果小于1秒，不刷新
                let pageMyCliekTime = uni.getStorageSync("shopPageMyInfo");
                if (pageMyCliekTime) {
                    let nowTime = new Date().getTime();
                    if (nowTime - pageMyCliekTime < 1000) {
                        return;
                    }
                }

                this. getBalanceSelfMap();
                // 获取当前时间
                uni.setStorageSync("pageMyCliekTime", new Date().getTime());
            }
        },
        getBalanceSelfMap() {
            this.$kt.request.request({
                api: this.$api.system.balance.getSelfMap,
                isNoInterceptor: true,
                stateSuccess: (res) => {
                    this.$system.setBalanceMap(res.data);
                    this.balanceSelfMap = res.data;
                },
            });
        },
    }
}
</script>

<style lang="scss" scoped>
.tabbar {
    position: fixed;
    text-align: center;
    bottom: 0rpx;
    width: 100%;
    left: 0;
    right: 0;
    height: 50px;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    z-index: 999;
    padding-top: 12rpx;

    .tabbar-item {
        text-align: center;
        width: 100%;

        .tabbar-item-icon {
            position: relative;
            text-align: center;
            margin-bottom: 2px;

            .img {
                width: 20px;
                height: 20px;
            }
        }

        .tabbar-item-text {
            text-align: center;
            font-size: 12px;

            color: #333;
        }
    }

    .tabbar-item-no-active {
        .tabbar-item-icon {
            .img {
                opacity: .5;
            }
        }

        .tabbar-item-text {
            opacity: .5;
        }
    }

    .tabbar-item-no-active:active {
        opacity: .7;
    }

    .tabbar-item-active {
        opacity: 1;
    }
}
</style>